import { pages } from '@/models/common';
import { IExposedProperty } from '@/models/property';
import app_store, { onAppDataChange } from '@/stores/app.store';
import booking_store, { calculateTotalCost } from '@/stores/booking';
import { checkout_store } from '@/stores/checkout.store';
import { formatAmount, getDateDifference } from '@/utils/utils';
import { Component, Host, State, h, Event, EventEmitter, Prop } from '@stencil/core';
import { format } from 'date-fns';
import IMask from 'imask';

@Component({
  tag: 'ir-booking-summary',
  styleUrl: 'ir-booking-summary.css',
  shadow: true,
})
export class IrBookingSummary {
  @Prop() isLoading = false;

  @State() property: IExposedProperty;

  componentWillLoad() {
    this.property = { ...app_store.property };
    onAppDataChange('property', (newValue: IExposedProperty) => {
      this.property = { ...newValue };
    });
  }

  @Event() routing: EventEmitter<pages>;
  @Event() bookingClicked: EventEmitter<null>;
  handleBooking() {
    this.bookingClicked.emit(null);
  }

  private getExpiryMask() {
    const currentYear = new Date().getFullYear() % 100; // Get last two digits of the current year
    return {
      mask: 'MM/YY',
      blocks: {
        MM: {
          mask: IMask.MaskedRange,
          from: 1,
          to: 12,
          maxLength: 2,
          placeholderChar: 'M',
        },
        YY: {
          mask: IMask.MaskedRange,
          from: currentYear,
          to: 99,
          maxLength: 2,
          placeholderChar: 'Y',
        },
      },
      lazy: false,
      placeholderChar: '_',
    };
  }
  render() {
    const total_nights = getDateDifference(booking_store.bookingAvailabilityParams.from_date ?? new Date(), booking_store.bookingAvailabilityParams.to_date ?? new Date());
    const { prePaymentAmount, totalAmount } = calculateTotalCost();
    return (
      <Host>
        <div class="w-full rounded-md bg-gray-100  text-sm lg:max-w-sm">
          <div class="aspect-[1/1] max-h-32 w-full lg:aspect-[16/9]">
            <img class="h-full w-full rounded-t-md object-cover" src={this.property.space_theme.background_image} alt="" />
          </div>
          <section class="flex flex-col items-center space-y-4 p-4 lg:p-6">
            {/* <h3 class="text-center  text-lg font-medium">{this.property.name}</h3> */}
            <div class="flex w-full flex-1 items-center ">
              <div class="w-56 rounded-md border border-gray-300 bg-white p-2 text-center text-xs">
                <p>Check-in</p>
                <p class="text-sm font-semibold">
                  {format(booking_store.bookingAvailabilityParams?.from_date ? new Date(booking_store.bookingAvailabilityParams?.from_date) : new Date(), 'eee, dd MMM yyyy')}
                </p>
                <p>From {app_store.property?.time_constraints.check_in_from}</p>
              </div>
              <div class="h-[1px] w-full min-w-[50px] flex-1 bg-gray-300 "></div>
              <div class="w-56 rounded-md border border-gray-300 bg-white p-2 text-center text-xs">
                <p>Check-out</p>
                <p class="text-sm font-semibold">
                  {format(booking_store.bookingAvailabilityParams?.to_date ? new Date(booking_store.bookingAvailabilityParams?.to_date) : new Date(), 'eee, dd MMM yyyy')}
                </p>
                <p>Before {app_store.property?.time_constraints.check_out_till}</p>
              </div>
            </div>

            <ir-button onButtonClick={() => this.routing.emit('booking')} label="Change details" variants="outline-primary" class="w-full"></ir-button>
            <div class={'mt-4  w-full'}>
              <ul class={'w-full space-y-2'}>
                <li class={'flex w-full items-center justify-between'}>
                  <span>
                    {total_nights} {total_nights > 1 ? 'Nights' : 'Night'}
                  </span>
                  <span>{formatAmount(totalAmount, app_store.userPreferences.currency_id)}</span>
                </li>
                {checkout_store.pickup?.location && (
                  <li class={'flex w-full items-center justify-between'}>
                    <span>Pickup fee</span>
                    <span>{formatAmount(checkout_store.pickup.location ? Number(checkout_store.pickup.due_upon_booking) : 0, app_store.userPreferences.currency_id)}</span>
                  </li>
                )}
                <li class={'flex w-full items-center justify-between'}>
                  <span>Total</span>
                  <span class="text-lg font-medium">
                    {formatAmount(totalAmount + (checkout_store.pickup.location ? Number(checkout_store.pickup.due_upon_booking) : 0), app_store.userPreferences.currency_id)}
                  </span>
                </li>
                {prePaymentAmount > 0 && (
                  <li class={'flex w-full items-center justify-between pt-1'}>
                    <span>Pay now</span>
                    <span class="text-base">{formatAmount(prePaymentAmount, app_store.userPreferences.currency_id)}</span>
                  </li>
                )}
              </ul>
            </div>
            <div class="divide-y rounded-md  border border-solid bg-white">
              {this.property?.allowed_payment_methods?.map(method => {
                if (method.code === '004')
                  return (
                    <div class="flex w-full gap-4 p-4" key={method.code}>
                      <ir-radio></ir-radio>
                      <div class={'flex-1 space-y-4'}>
                        <fieldset>
                          <ir-input placeholder="" label="Name on card" class="w-full"></ir-input>
                        </fieldset>
                        <ir-credit-card-input></ir-credit-card-input>
                        <div class="flex flex-col gap-2.5 sm:flex-row sm:items-center">
                          <fieldset class="w-full">
                            <ir-input type="text" placeholder="MM/YY" mask={this.getExpiryMask()} label="Expiration date" class="w-full" rightIcon>
                              <svg slot="right-icon" width="20" height="16" viewBox="0 0 20 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M0 3C0 1.34315 1.34315 0 3 0H17C18.6569 0 20 1.34315 20 3V13C20 14.6569 18.6569 16 17 16H3C1.34315 16 0 14.6569 0 13V3Z" fill="#EAECF0" />
                                <path d="M2 8C2 7.44772 2.44772 7 3 7H17C17.5523 7 18 7.44772 18 8C18 8.55228 17.5523 9 17 9H3C2.44772 9 2 8.55228 2 8Z" fill="#8B8B8B" />
                                <path d="M2 4C2 3.44772 2.44772 3 3 3H5C5.55228 3 6 3.44772 6 4C6 4.55228 5.55228 5 5 5H3C2.44772 5 2 4.55228 2 4Z" fill="white" />
                                <path
                                  d="M10 11C10 10.4477 10.4477 10 11 10H15C15.5523 10 16 10.4477 16 11V13C16 13.5523 15.5523 14 15 14H11C10.4477 14 10 13.5523 10 13V11Z"
                                  fill="#FE4F42"
                                />
                                <path d="M11 11H15V13H11V11Z" fill="#EAECF0" />
                              </svg>
                            </ir-input>
                          </fieldset>
                          <fieldset class="w-full">
                            <ir-input label="Security code" maxlength={4} placeholder="XXXX" class="w-full" rightIcon>
                              <svg slot="right-icon" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path
                                  fill-rule="evenodd"
                                  clip-rule="evenodd"
                                  d="M2 7C2 6.20435 2.31607 5.44129 2.87868 4.87868C3.44129 4.31607 4.20435 4 5 4H19C19.7956 4 20.5587 4.31607 21.1213 4.87868C21.6839 5.44129 22 6.20435 22 7V8H2V7ZM2 10V17C2 17.7956 2.31607 18.5587 2.87868 19.1213C3.44129 19.6839 4.20435 20 5 20H19C19.7956 20 20.5587 19.6839 21.1213 19.1213C21.6839 18.5587 22 17.7956 22 17V10H2ZM7 12C6.73478 12 6.48043 12.1054 6.29289 12.2929C6.10536 12.4804 6 12.7348 6 13C6 13.2652 6.10536 13.5196 6.29289 13.7071C6.48043 13.8946 6.73478 14 7 14H12C12.2652 14 12.5196 13.8946 12.7071 13.7071C12.8946 13.5196 13 13.2652 13 13C13 12.7348 12.8946 12.4804 12.7071 12.2929C12.5196 12.1054 12.2652 12 12 12H7Z"
                                  fill="#EAECF0"
                                />
                                <rect x="14.5" y="11.5" width="6" height="3" rx="0.5" stroke="#FE4F42" />
                              </svg>
                            </ir-input>
                          </fieldset>
                        </div>
                      </div>
                    </div>
                  );
                if (method.code === '005') {
                  return (
                    <div class="flex w-full gap-4  p-4">
                      <ir-radio></ir-radio>
                      <div class="flex-1 space-y-1.5">
                        <p>{method.description}</p>
                        <p class="text-xs text-gray-700">{method.data?.map(d => <span key={d.key}>{d.value}</span>)}</p>
                      </div>
                    </div>
                  );
                }
              })}
            </div>
            <div class={'w-full'}>
              <ir-checkbox label="I agree to the privacy policy."></ir-checkbox>
            </div>
            <ir-button isLoading={this.isLoading} size="md" class="w-full" label="CONFIRM BOOKING" onButtonClick={this.handleBooking.bind(this)}></ir-button>
          </section>
        </div>
      </Host>
    );
  }
}
