@import url('../../gloabls.css');
:host {
  display: block;
}
.select-label {
  /* transform-origin: left top; */
  top: -0.8rem;
  background: white;
  left: calc(0.875rem - 0.3rem);
}

select {
  appearance: none;
  outline: none;
  background: transparent;
}
select:focus,
select:focus-visible {
  border-color: hsl(var(--brand-300, 206, 100%, 76%));
}
.select-icon[data-variant='double-line']:dir(rtl) {
  left: 1rem;
}
.select-icon[data-variant='double-line']:dir(ltr) {
  right: 1rem;
}
.select-icon[data-variant='default']:dir(rtl) {
  left: 6px;
}
.select-icon[data-variant='default']:dir(ltr) {
  right: 6px;
}
select option {
  text-indent: 100px; /* Adjust the value as needed */
}
